.form {
    display: flex;
    border: 1px solid #ccc;
    flex-direction: column;
    flex-wrap: wrap;
    align-content: flex-start;
    justify-content: space-evenly;
    align-items: flex-end;
    width: 250px;
    padding: 5px;
    margin: 0 auto;
}

input {
    margin-bottom: 15px;
    margin-left: 15px;
}

.button {
    border: none;
    background-color: #3DB2FF;
    padding: 5px 10px;
    color: #fff;
    border-radius: 5px;
}